








































































































import MessageBox from "@/utils/message";
import { Component, Emit, Prop, Vue } from "vue-property-decorator";
import api from "@/microApps/buildingManage/api";
import { ElTable } from "element-ui/types/table";

@Component({})
export default class BuildingFloorDevice extends Vue {
  //表头样式
  tableHeader: any = {
    "background-color": "#F8F8F9",
    color: "#333333",
    "font-size": "14px"
  };

  @Prop({ type: String })
  buildId!: any;

  //建筑绑定的设备
  buildList: Array<any> = [];

  //未绑定的设备
  noBindbuildList: Array<any> = [];

  //未绑定设备弹窗
  noBindVechcleDialogVisible: boolean = false;

  //已选择的设备
  selectedDevice: Array<any> = [];

  //未绑定设备筛选条件
  pageParam: any = {
    size: 10,
    current: 1,
    total: 0,
    query: {
      name: ""
    }
  };

  handleSelectionChange(val: Array<any>) {
    this.selectedDevice = val;
  }

  bindType: any = "建筑";

  floorId: String = "";

  floorOptions: Array<any> = [];
  loading = false;

  //获取floor的option对象
  getFloorOption() {
    if (this.bindType == "楼层") {
      this.$http
        .get(api.getAllFloorList, {
          params: {
            buildId: this.buildId
          }
        })
        .then((res: any) => {
          if (res.data.status) {
            this.floorOptions = res.data.data;
          } else {
            this.$message.error(res.data.message);
          }
        });
    } else {
      this.floorOptions = [];
      this.floorId = "";
    }
  }

  //TODO 获取未绑定的设备列表
  getNoBindList() {
    this.pageParam.query.bound = false;
    this.$http.post(api.getDevicePaging, this.pageParam).then((res: any) => {
      if (res.data.status) {
        this.noBindbuildList = res.data.data.records;
        this.pageParam.total = res.data.data.total;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //保存绑定
  saveBind() {
    if (this.bindType == "楼层" && this.floorId == "") {
      return this.$message.warning("请选择楼层");
    }
    if (this.selectedDevice.length == 0) {
      return this.$message.warning("请选择设备");
    }
    this.loading = true;
    let devices = this.selectedDevice.map((item: any) => {
      return {
        buildingId: this.buildId,
        floorId: this.floorId,
        deviceId: item.id,
        outMark: false
      };
    });
    this.$http
      .post(api.bindDevice + this.buildId, {
        floorId: this.floorId,
        devices: devices
      })
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.$message.success("操作成功");
          this.noBindVechcleDialogVisible = false;
          this.getList();
        } else {
          this.$message.error(res.data.messages);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  //TODO 获取绑定列表
  getList() {
    this.$http.get(api.getBindDevice + this.buildId).then((res: any) => {
      if (res.data.status) {
        // console.log("建筑绑定设备", res.data.data);
        this.buildList = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  handleSize(val: any) {
    this.pageParam.size = val;
    this.getNoBindList();
  }

  handleCurrent(val: any) {
    this.pageParam.current = val;
    this.getNoBindList();
  }

  //TODO 取消绑定
  cancelBind(device: any) {
    MessageBox.confirm("确认需要取消绑定？").then(async () => {
      let {
        data: { data, status, message }
      } = await this.$http.post(api.cancleDevice, {
        buildingId: this.buildId,
        floorId: device.floorId,
        deviceId: device.id,
        outMark: device.outMark
      });
      if (status) {
        this.$message.success("操作成功");
        this.getList();
      } else {
        this.$message.error(message);
      }
    });
  }

  changeBindGps(data: any) {
    // console.log(data);
    this.$http
      .get(api.switchPlace + data.relationId, {
        params: {
          outMark: data.outMark
        }
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("已切换为" + (data.outMark ? "室外" : "室内"));
        } else {
          this.$message.error(res.data.status);
        }
      });
  }

  bindVechcle() {
    this.floorOptions = [];
    this.floorId = "";
    this.selectedDevice = [];
    this.bindType = "建筑";
    this.noBindVechcleDialogVisible = true;
    this.pageParam = {
      size: 10,
      current: 1,
      total: 0,
      query: {
        name: ""
      }
    };
    this.getNoBindList();
  }

  mounted() {
    this.getList();
  }
}
