











































































































































































import { Vue, Component, Emit, Prop, Watch } from "vue-property-decorator";
import BuildingFloorList from "./BuildingFloorList.vue";
import BuildingFloorDevice from "./BuildingFloorDevice.vue";
import Utils from "@/utils/utils";
import api from "@/microApps/buildingManage/api";
import { ElTree } from "element-ui/types/tree";
import { ElForm } from "element-ui/types/form";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";

const AMap: any = (window as any).AMap;
const AMapUI: any = (window as any).AMapUI;
@Component({
  components: { BuildingFloorList, BuildingFloorDevice }
})
export default class BuildingDetails extends Vue {
  loading: any = false;

  @Prop({ type: String })
  listRowId!: string;

  // 父组件传值
  customOptions: Array<any> = [];
  // 基本属性表单(没有包括自定义属性)
  detailsForm: any = {};
  // 基本属性表单规则(没有包括自定义属性)
  detailsFormRules: any = {
    name: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    // code: [
    //   {
    //     required: true,
    //     message: "该选项为必填项",
    //     trigger: "change"
    //   }
    // ],
    orgId: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    typeDetailId: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ]
  };
  // 当前用户能看到的所有组织
  orgOptions: Array<any> = [];
  orgVisible: boolean = false;
  // typedetail类型树
  typeDetailOptions: Array<any> = [];
  // 输入框是否可输入，用于查看情况
  disabled: boolean = false;
  //隐藏确定按钮，用于查看情况
  visible: boolean = true;

  //  类型树props
  typeVisible: boolean = false;

  hasCustomRole: boolean = qTouch.getBaseTypeByCode("building");

  // 地理位置选择
  map: any;
  marker: any = null;
  addressDialog = false;
  text = "建筑物坐标绑定";
  showLeaveChoose = false;
  zoom = 18;
  center = [106.569137124949, 29.643814122592];

  @Watch("listRowId", { immediate: true })
  listRowIdChange() {
    this.listRowId && this.getBuildingInfo(this.listRowId);
    this.getOrgTree();
    this.getTypeDetailTree();
  }

  // 切换到车辆列表页面（添加和编辑）
  @Emit("switch")
  switchToList() {}

  // 获得组件CustomInput填充之后的数据
  async getFilledCustomOptions() {
    let data: any;
    let customInput: any = this.$refs["custom-input"];
    if (customInput) {
      data = await customInput.formValidation();
    }
    if (data) {
      this.detailsForm.extInfo = data;
    }
    this.doSave(); //请求后台保存
  }

  // 保存信息
  saveInfo() {
    //验证基本信息表单
    (this.$refs.detailsForm as any).validate((valid: any) => {
      if (valid) {
        if (this.detailsForm.needUpload) {
          this.loading = true;
          delete this.detailsForm.needUpload;
          (this.$refs.upload as ElUpload).submit();
        } else {
          //处理子组件自定义信息数据
          this.getFilledCustomOptions();
        }
      }
    });
  }

  //获取类型树
  getTypeDetailTree() {
    this.queryTree(api.findTypeTree + "/building", "building");
  }

  // 类型树只允许单选
  handleTypeChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.typeTree as any).setCheckedKeys([data.id]);
    }
  }

  // 确定类型
  saveType() {
    // let typeDetailIds = (this.$refs.typeTree as ElTree).getCheckedKeys();
    // this.$set(this.detailsForm, "typeDetailId", typeDetailIds.length ? typeDetailIds[0] : "");
    (this.$refs.detailsForm as ElForm).validateField("typeDetailId", () => {});
    // this.typeVisible = false;
    if (this.detailsForm.typeDetailId) {
      this.changeCustomOptions();
    }
  }

  //获取组织树
  getOrgTree() {
    this.queryTree(api.getNewOrgTree, "organization");
  }

  // 类型树只允许单选
  handleOrgChange(data: any, checked: boolean, node: boolean) {
    if (checked) {
      (this.$refs.orgTree as any).setCheckedKeys([data.id]);
    }
  }

  // 确定类型
  saveOrg() {
    // let typeDetailIds = (this.$refs.orgTree as ElTree).getCheckedKeys();
    // if (typeDetailIds.length) {
    //   this.detailsForm.orgId = typeDetailIds[0];
    // } else {
    //   this.detailsForm.orgId = "";
    // }
    // this.orgVisible = false;
  }

  //获取树数据，组织树/类型树
  queryTree(url: any, obj: any) {
    this.$http.get(url).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          Utils.removeEmptyChildren(res.data.data);
          if (obj == "building") {
            this.typeDetailOptions = res.data.data;
            // this.typeVisible = true;
            // this.$nextTick(() => {
            //   (this.$refs.typeTree as ElTree).setCheckedKeys([this.detailsForm.typeDetailId]);
            // });
          }
          if (obj == "organization") {
            this.orgOptions = res.data.data;
            // this.orgVisible = true;
            // this.$nextTick(() => {
            //   (this.$refs.orgTree as ElTree).setCheckedKeys([this.detailsForm.orgId]);
            // });
          }
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  //类型变化时重新渲染自定义信息
  changeCustomOptions() {
    this.$http.get(api.getTemplateByTypeDetaiId + "/" + this.detailsForm.typeDetailId).then((res: any) => {
      if (res.data.status) {
        this.customOptions = res.data.data;
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  //请求后台，执行保存
  doSave() {
    this.loading = true;
    let url = api.add;
    if (this.listRowId) {
      url = api.update;
    }
    this.$http
      .post(url, this.detailsForm)
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success(res.data.message);
          res.data.data && this.$emit("update:listRowId", res.data.data);
          this.switchToList();
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  // 自定义上传文件
  customHttpRequest(data: any) {
    this.loading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http
      .post(data.action, fData.formData, fData.config)
      .then((response: any) => {
        this.loading = false;
        if (response.data.status) {
          this.detailsForm.image = response.data.data.accessUrl;
          this.doSave(); //请求后台保存
        } else {
          this.$message.error("图片上传失败");
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.detailsForm, "image", file.url);
      this.detailsForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.detailsForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }

  getBuildingInfo(buildingId: any) {
    this.loading = true;
    this.$http
      .get(api.getOneById + "/" + buildingId)
      .then((response: any) => {
        if (response.data.status) {
          this.detailsForm = response.data.data;
          if (this.detailsForm.extInfoVO) {
            this.customOptions = this.detailsForm.extInfoVO;
          }
        } else {
          this.$message.error(response.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  // 绑定地理信息
  bindAddress() {
    this.addressDialog = true;
    this.$nextTick(() => {
      // 已绑定
      if (this.detailsForm.location) {
        let lnglat = JSON.parse(this.detailsForm.location);
        this.initMap(lnglat); // 将地图中心调整为 该建筑坐标
        this.addBuildMarker(lnglat, this.detailsForm.address); // 在地图上显示该点
        this.showLeaveChoose = false;
        this.text = "当前建筑绑定坐标为：" + this.detailsForm.location;
      }
      // 未绑定
      else {
        this.text = "请在地图上选取建筑物位置，并双击确定！";
        // 调用地图交互事件
        this.initMap(this.center);
        this.chooseSiteOnMap();
      }
    });
  }

  // 载入地图
  initMap(center: any) {
    const _this = this;
    this.map = Object.freeze(
      new AMap.Map("buildAmap", {
        center: center,
        resizeEnable: true,
        zoom: this.zoom,
        features: ["bg", "point", "road", "building"]
      })
    );
    this.map.setMapStyle("amap://styles/7a2258c5592a544ff4242a2d0341ef6c");
    AMapUI.loadUI(["misc/PoiPicker"], function(PoiPicker: any) {
      var poiPicker = new PoiPicker({
        input: "buildInput"
      });
      //初始化poiPicker
      _this.poiPickerReady(poiPicker);
    });
  }

  poiPickerReady(poiPicker: any) {
    const _this = this;
    (window as any).poiPicker = poiPicker;
    let marker = new AMap.Marker();
    // let infoWindow = new AMap.InfoWindow({
    //   offset: new AMap.Pixel(0, -20)
    // });
    // 选取了某个POI
    poiPicker.on("poiPicked", function(poiResult: any) {
      let source = poiResult.source;
      let poi = poiResult.item;
      let info = {
        source: source,
        id: poi.id,
        name: poi.name,
        location: poi.location.toString(),
        address: poi.formattedAddress
      };
      marker.setMap(_this.map);
      // infoWindow.setMap(_this.map);
      marker.setPosition(poi.location);
      // infoWindow.setPosition(poi.location);
      // // infoWindow.setContent("POI信息: <pre>" + JSON.stringify(info, null, 2) + "</pre>");
      // infoWindow.open(_this.map, marker.getPosition());
      _this.map.setCenter(marker.getPosition());
    });
  }

  // 地图上显示marker标点
  addBuildMarker(position: any, address: string) {
    let _this = this;
    this.marker = new AMap.Marker({
      position: position
    });
    this.marker.setLabel({
      //修改label相对于maker的位置
      offset: new AMap.Pixel(20, -20),
      content: "<div style='margin: 2px 5px;font-family: PingFang SC;line-height: 16px'>" + address + "</div>"
    });
    this.marker.setMap(this.map);
  }

  // 结束坐标选取，重新载入地图
  chooseOff() {
    this.text = "建筑物坐标绑定";
    this.showLeaveChoose = false;
    this.initMap(this.center);
  }

  // 开始地图交互事件
  chooseSiteOnMap() {
    let _this = this;
    this.showLeaveChoose = true;
    this.text = "请在地图上选取建筑物位置，并双击确定！";
    this.map.clearMap();
    // 修改鼠标样式( 十字型 )
    this.map.setDefaultCursor("crosshair");
    this.map.on("dblclick", function(e: any) {
      let lnglat = [e.lnglat.getLng(), e.lnglat.getLat()];
      _this.text = "您选取了 " + JSON.stringify(lnglat) + " 的位置！";
      let geocoder = new AMap.Geocoder({
        radius: 1000,
        extensions: "all"
      });
      geocoder.getAddress(lnglat, function(status: any, result: any) {
        if (status === "complete" && result.info === "OK") {
          let address = result.regeocode.formattedAddress; //返回地址描述
          _this
            .$prompt("当前选中位置：", "提示", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              inputValue: address
            })
            .then((obj: any) => {
              _this.detailsForm.location = JSON.stringify(lnglat);
              _this.detailsForm.address = obj.value;
              _this.addressDialog = false;
            })
            .catch(() => {
              // _this.$message.info("请重新选择");
              _this.text = "请在地图上选取建筑物位置，并双击确定！";
            });
        }
      });
    });
  }

  destroyMap() {
    this.map.destroy();
    console.log(this.map);
  }
}
