
































































import { Vue, Component, Emit } from "vue-property-decorator";
import api from "@/microApps/buildingManage/api";

@Component({})
export default class BuildingList extends Vue {
  // 表格信息
  tableData: any = [];
  // 分页信息
  pagination: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      name: "",
      code: "",
      typeDetailId: ""
    }
  };
  loading = false;
  //复选框选中行数据
  multipleSelection: Array<any> = [];

  //根据关键字，每页查询数量，当前页获得所有链表
  search() {
    this.pagination.current = 1;
    this.getList();
  }

  getList() {
    this.loading = true;
    this.$http
      .post(api.getPaging, this.pagination)
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.pagination.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleCurrentChange(val: number) {
    this.pagination.current = val;
    this.getList();
  }

  handleSizeChange(val: number) {
    this.pagination.size = val;
    this.getList();
  }

  //获得所有复选框选中数据
  getAllSelected(val: Array<any>) {
    this.multipleSelection = val;
  }

  //单个删除
  handleDelete(row: any) {
    let ids: any = [];
    ids.push(row.id);
    this.delete(ids);
  }

  //批量删除
  multipleDelete() {
    let ids: any = [];
    this.multipleSelection.forEach(item => {
      ids.push(item.id);
    });
    this.delete(ids);
  }

  delete(ids: any) {
    if (ids.length === 0) {
      this.$message.warning("请选择要删除的数据！");
    } else {
      this.$confirm("是否确认删除数据？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        // 删除操作
        this.$http.post(api.multipleDelete, ids).then((response: any) => {
          if (response.data.status) {
            this.$message.success(response.data.message);
            if (this.tableData.length === ids.length) {
              this.pagination.current == 0 ? 0 : (this.pagination.current -= 1);
            }
            this.getList();
          } else {
            this.$message.error(response.data.message);
          }
        });
      });
    }
  }

  //切换到详情页面（添加和编辑）
  @Emit("switch")
  switchToDetails() {}

  mounted() {
    this.getList(); //获取表格
  }
}
