






























































































































































import { Vue, Component, Prop } from "vue-property-decorator";
import Utils from "@/utils/utils";
import api from "@/microApps/buildingManage/api";
import Message from "@/utils/message";
import { ElUpload, ElUploadInternalFileDetail } from "element-ui/types/upload";
import { validateImage } from "@/utils/validate";

@Component({})
export default class BuildingFloorList extends Vue {
  method: any = "";

  //父组件传值
  @Prop({ type: String, required: true })
  buildingId!: any;

  floorDialogVisible: boolean = false;
  //=========楼层============
  floorTableData: any = [];
  floorInfoForm: any = {};
  floorFormRules: any = {
    name: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ],
    code: [
      {
        required: true,
        message: "该选项为必填项",
        trigger: "change"
      }
    ]
  };
  // 分页信息
  pagination: any = {
    // 显示页，用于显示某页数据
    current: 1,
    // 总条数
    total: 0,
    // 查询页，作为查询某页数据的参数
    size: 10,
    // 查询
    query: {
      name: "",
      code: ""
    }
  };
  dialogImgVisible: any = false;

  //未绑定传感器分页
  pageParam: any = {
    size: "1",
    current: "10",
    query: ""
  };

  bindDeviceList: Array<any> = [];
  loading: boolean = false;

  //获取楼层的所有传感器
  getFloorDeviceList(floorId: any) {
    this.$http
      .get(api.getBindDevice + this.buildingId, {
        params: {
          floorId: floorId
        }
      })
      .then((res: any) => {
        if (res.data.status) {
          this.bindDeviceList = res.data.data;
        } else {
          this.$message.error(res.data.message);
        }
      });
  }

  //=============楼层==================
  //获取楼层表
  getFloorList() {
    this.$http.post(api.getFloorPaging + "/" + this.buildingId, this.pagination).then((res: any) => {
      if (res.status) {
        if (res.data.status) {
          this.floorTableData = res.data.data.records;
          this.pagination.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  //新增楼层
  addFloorForm() {
    this.floorDialogVisible = true;
    this.floorInfoForm.buildingId = this.buildingId;
    this.floorInfoForm = {};
    this.bindDeviceList = [];
  }

  //编辑楼层信息
  editFloor(row: any) {
    this.floorInfoForm = row;
    this.getFloorDeviceList(this.floorInfoForm.id);
    this.floorDialogVisible = true;
  }

  mounted() {
    this.getFloorList();
  }

  //关闭弹窗
  closeDialog() {
    this.floorInfoForm = {};
    this.floorDialogVisible = false;
  }

  handleFloorDelete(index: any, row: any) {
    Message.confirm("是否需要删除该楼层?").then(() => {
      this.$http.get(api.deleteBuildingFlorr + row.id).then(res => {
        if (res.data.status) {
          this.$message.success("删除成功！");
          this.getFloorList();
        } else {
          this.$message.error(res.data.message);
        }
      });
    });
  }

  // 自定义上传文件
  customHttpRequest(data: any) {
    this.loading = true;
    let fData = Utils.createUploadForm(data.file);
    this.$http.post(data.action, fData.formData, fData.config).then((response: any) => {
      this.loading = false;
      if (response.data.status) {
        this.floorInfoForm.image = response.data.data.accessUrl;
        this.doSubmit();
      } else {
        this.$message.error("图片上传失败");
      }
    });
  }

  customChange(file: ElUploadInternalFileDetail, fileList: ElUploadInternalFileDetail[]) {
    let { status, message } = validateImage(file.raw);
    if (status) {
      this.$set(this.floorInfoForm, "image", file.url);
      this.floorInfoForm.needUpload = true;
    } else {
      this.$message.error(message);
      (this.$refs.upload as any).handleRemove(file);
    }
  }

  handleRemove() {
    this.floorInfoForm.image = "";
    this.$nextTick(() => {
      (this.$refs.upload as ElUpload).clearFiles();
    });
  }

  // 保存楼层
  saveFloor() {
    //验证基本信息表单
    (this.$refs.floorInfoForm as any).validate((valid: any) => {
      if (valid) {
        this.loading = true;
        if (this.floorInfoForm.needUpload) {
          delete this.floorInfoForm.needUpload;
          (this.$refs.upload as ElUpload).submit();
        } else {
          this.doSubmit();
        }
      }
    });
  }

  doSubmit() {
    let url = api.updateFloor;
    if (this.floorInfoForm.id == null) {
      url = api.addFloor;
    }
    if (this.buildingId == "") {
      return this.$message.warning("建筑不存在");
    }
    this.floorInfoForm.buildingId = this.buildingId;
    this.$http.post(url, this.floorInfoForm).then((res: any) => {
      this.loading = false;
      if (res.status) {
        if (res.data.status) {
          this.$message.success(res.data.message);
          this.floorDialogVisible = false;
          this.getFloorList();
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }
}
