






import { Vue, Component } from "vue-property-decorator";
import BuildingList from "./components/BuildingList.vue";
import BuildingDetails from "./components/BuildingDetails.vue";

@Component({
  components: {
    BuildingList,
    BuildingDetails
  }
})
export default class BuildingManage extends Vue {
  whichComponent: any = "BuildingList";
  listRowId: string = "";
  method: any = "";

  switchComponent(buttonName: String, row: any) {
    switch (buttonName) {
      case "back":
        this.whichComponent = "BuildingList";
        break;
      case "add":
        this.whichComponent = "BuildingDetails";
        this.listRowId = "";
        break;
      case "edit":
        this.whichComponent = "BuildingDetails";
        this.listRowId = row.id;
        break;
      default:
        this.whichComponent = "BuildingList";
    }
  }
}
